import React, { useState, useMemo } from 'react';
import { Container, Table, Form, Row, Col } from 'react-bootstrap';
import { ArrowUp, ArrowDown } from 'lucide-react';
import { useData } from './DataLoader';
import { useLanguage } from '../components/LanguageProvider';

const PublicationsComponent = () => {

  const {language} = useLanguage();
  const { data } = useData();
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState({ key: 'year', direction: 'descending' });

  const sortedPublications = useMemo(() => {
    if (!data.publications) return [];
    
    let sortableItems = [...data.publications];
    if (sortConfig.key !== null) {
      sortableItems.sort((a, b) => {
        if (sortConfig.key === 'citations' || sortConfig.key === 'year') {
          // Numeric sorting for citations and year
          return sortConfig.direction === 'ascending'
            ? Number(a[sortConfig.key]) - Number(b[sortConfig.key])
            : Number(b[sortConfig.key]) - Number(a[sortConfig.key]);
        }
        // String sorting for title
        return sortConfig.direction === 'ascending'
          ? a[sortConfig.key].localeCompare(b[sortConfig.key])
          : b[sortConfig.key].localeCompare(a[sortConfig.key]);
      });
    }
    return sortableItems;
  }, [data.publications, sortConfig]);

  const filteredPublications = useMemo(() => {
    if (!sortedPublications) return [];
    
    return sortedPublications.filter(pub => 
      pub.titre.toLowerCase().includes(searchTerm.toLowerCase()) ||
      pub.auteurs.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [sortedPublications, searchTerm]);

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
  };

  const requestSort = (key) => {
    let direction = 'ascending';
    if (sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending';
    }
    setSortConfig({ key, direction });
  };

  const getSortIcon = (key) => {
    if (sortConfig.key === key) {
      return sortConfig.direction === 'ascending' 
        ? <ArrowUp className="inline-block ml-1" size={16} /> 
        : <ArrowDown className="inline-block ml-1" size={16} />;
    }
    return null;
  };

  const handleRowClick = (doi) => {
    if (doi) {
      window.open(doi, '_blank', 'noopener,noreferrer');
    }
  };

  /*if (loading) return <div className="text-center my-5">{language !== 'fr' ? 'Loading publications...' : 'Chargement des publications...'} </div>;
  if (error) return <div className="text-center my-5">{language !== 'fr' ? 'Error loading publications' : 'Erreur de chargement des publications'}</div>;
*/
  return (
    <Container className="my-5">
      <Row className="mb-4">
        <Col>
          <Form.Control 
            type="text" 
            value={searchTerm} 
            onChange={handleSearchChange} 
            placeholder= {language !== 'fr' ? 'Search by title or author' : "Chercher par titre ou auteur" }
            className="form-control-lg"
          />
        </Col>
      </Row>

      <Table hover responsive>
        <thead>
          <tr>
            <th 
              style={{width: '60%', cursor: 'pointer'}} 
              onClick={() => requestSort('titre')}
              className="align-middle"
            >
              {language !== 'fr' ? 'Title' : "Titre" } {getSortIcon('titre')}
            </th>
            <th 
              className="text-center align-middle" 
              style={{cursor: 'pointer', width: '15%'}} 
              onClick={() => requestSort('citations')}
            >
              {language !== 'fr' ? 'Cited by' : "Citations" } {getSortIcon('citations')}
            </th>
            <th 
              className="text-center align-middle" 
              style={{cursor: 'pointer', width: '15%'}} 
              onClick={() => requestSort('annee')}
            >
             {language !== 'fr' ? 'Year' : "Année" }  {getSortIcon('annee')}
            </th>
          </tr>
        </thead>
        <tbody>
          {filteredPublications.map((pub, index) => (
            <tr 
              key={index} 
              onClick={() => handleRowClick(pub.doi)}
              style={{cursor: pub.doi ? 'pointer' : 'default'}}
              className="hover:bg-gray-50"
            >
              <td>
                <h5 className="text-primary mb-2" style={{color: '#1a0dab'}}>
                  {pub.titre}
                </h5>
                <p className="text-muted mb-1" style={{fontStyle: 'italic'}}>
                  {pub.auteurs}
                </p>
                <p className="text-warning mb-0" style={{color: '#D5A00F'}}>
                  {pub.publication}
                </p>
              </td>
              <td className="text-center align-middle">{pub.citations}</td>
              <td className="text-center align-middle">{pub.annee}</td>
            </tr>
          ))}
        </tbody>
      </Table>
      
      {filteredPublications.length === 0 && (
        <div className="text-center mt-4 p-4 bg-light rounded">
          <p className="mb-0"> {language !== 'fr' ? 'No publications match your search.' : "Aucune publication ne correspond à votre recherche." }</p>
        </div>
      )}
    
    </Container>
  );
};

export default PublicationsComponent;